

import React, { useEffect, useState } from 'react'
import Edit from './edit'
import { navigate } from 'hookrouter'
import { EditModal , Button, RowItem, SectionTitle } from 'Components'
import { useQuery, useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import './style.scss'

const GET_PEOPLE = gql`
{
  Person{
    personId
    name
    jobTitle
    image
    twitter
    
  }
}`


export default function Departments() {
  const [ editing, setEditing ] = useState(false)
  const { loading, error, data, refetch } = useQuery(GET_PEOPLE)

  function onCancelModal(){
    setEditing(false)
    refetch().then((data) => {
      
    })    
  }

  if (error) {
  	console.log('error',error)
  	return <p>Error</p>
  }
  if (loading) return <p>Loading</p>

  return (
    <div className="DepartmentsContainer">
      <SectionTitle title="People" onAdd={() => setEditing(true)}/>
      {data && data.Person && data.Person.map((item, i) => {
        return(
          <RowItem key={i} onEdit={() => setEditing(item)} onClick={() => navigate(`person/${item.personId}`)}>
            <div className="row">
            <div className="logo cover" style={item.image ? {backgroundImage:`url(${item.image})`} : {border:'1px solid black'}} />
            <div className="column">
            <p>{item.name}</p>
           <p className="subText">{item.jobTitle}</p>
            </div>
            
            </div>
          </RowItem>
        )
      })}
      {editing &&
        <EditModal >
          <Edit {...editing} onCancel={onCancelModal}/>
        </EditModal>
      }
    </div>
  )
}

